.textarea {
  width: 100%;
  height: 150px;
  padding: 10px;
  margin-bottom: 40px;

  box-sizing: border-box;
  border: none;
  border-radius:  10px;
  box-shadow: inset -1px -1px 2px $color-dark-shadow;
  
  font-size: 20px;
  resize: none;
  
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: none;
  }
}